<template>
  <v-main fluid>
    <title-page title="Cotización" />
    <v-main style="padding-top: 2%;padding-left: 10%;padding-right: 12%">
      <v-form ref="form" v-model="valid" lazy-validation>
        <v-text-field
          v-model="name"
          :rules="nameRules"
          text="flash"
          label="Nombre"
          required
        ></v-text-field>

        <v-text-field
          v-model="email"
          :rules="emailRules"
          text="'flash@hotmail.com'"
          label="E-mail"
          required
        ></v-text-field>

        <v-text-field
          v-model="phone"
          :rules="phoneRules"
          placeholder="213456"
          label="Telefono"
          required
        ></v-text-field>
        <v-textarea
          v-model="message"
          filled
          label="Descripcion "
          auto-grow
          value=""
        ></v-textarea>
        <v-file-input
          v-model="file"
          color="deep-purple accent-4"
          counter
          label="Agregar Archivos"
          placeholder="Selecciona Tu Archivo"
          prepend-icon="mdi-paperclip"
          outlined
          :show-size="1000"
        >
          <template v-slot:selection="{ index, text }">
            <v-chip
              v-if="index < 1"
              color="deep-purple accent-4"
              dark
              label
              small
            >
              {{ text }}
            </v-chip>

            <span
              v-else-if="index === 2"
              class="overline grey--text text--darken-3 mx-2"
            >
              +{{ file.length - 2 }} File(s)
            </span>
          </template>
        </v-file-input>
        <div class="text-center">
          <v-btn
            :disabled="!valid"
            right
            color="warning"
            class="mr-4"
            @click="sendForm"
          >
            Enviar
          </v-btn>
        </div>
      </v-form>
    </v-main>
  </v-main>
</template>

<script>
import title_page from "../components/title_page";
//import postRequest from "@/plugins/Api.js";
import axios from "axios";
export default {
  components: {
    "title-page": title_page,
  },
  data() {
    return {
      valid: false,
      name: "",
      nameRules: [
        (v) => !!v || "Name is required",
        (v) => (v && v.length <= 15) || "Name must be less than 10 characters",
      ],
      phoneRules: [
        (v) => !!v || "Ingrese un Celular",
        (v) =>
          (v && v.length <= 13) ||
          "El celular debe tener menos de 13 Caracteres",
      ],
      email: "",
      emailRules: [
        (v) => !!v || "E-mail is required",
        (v) => /.+@.+\..+/.test(v) || "E-mail must be valid",
      ],
      select: null,
      items: ["Item 1", "Item 2", "Item 3", "Item 4"],
      checkbox: false,
      phone: "",
      file: null,
      message: "",
    };
  },
  methods: {
    async sendForm() {
      if (!this.$refs.form.validate()) {
        alert("Completa todos los campos por favor");
        return;
      }
      const files = [this.file];
      const filesBase64 = await Promise.all(
        files.filter((f) => !!f).map(this.toBase64)
      );
      const data = {
        name: this.name,
        email: this.email,
        phone: this.phone,
        message: this.message,
        file: filesBase64[0],
      };
      axios
        .post(
          "https://us-central1-flashcopias-59cb4.cloudfunctions.net/helloWorld",
          data
        )
        .then((response) => {
          alert("Formulario enviado con exito, gracias.");
          console.log(response);
        })
        .catch((e) => {
          console.log(e);
          alert("Error al enviar archivo, Intenta Nuevamente");
        });
      this.$router.replace("/");
    },
    toBase64(file) {
      return new Promise((resolve, reject) => {
        const reader = new FileReader();
        reader.readAsDataURL(file);
        reader.onload = () => resolve(reader.result);
        reader.onerror = (error) => reject(error);
      });
    },
  },
};
</script>
